body {
  font-family: "Roboto", sans-serif;
}

.content-name {
font-size: 19px;
}

p a {
color: #0a0a23;
text-decoration: underline;
}
p a:hover {
color: #0a0a23;
text-decoration: none;
}

button {
background-color: #F53838;
}

button:hover {
background-color: #0371e8;
}
button a {
color: #fff;
}

button a:hover {
color: #f0f0f0;
text-decoration: none;
transition: 0.6s;
}

pre {
overflow-x: auto;
margin: 1.5em 0 3em;
padding: 20px;
max-width: 100%;
color: #1b1b32;
font-size: 1.4rem;
line-height: 1.5em;
background: #f0f0f0;
}

img {
width: 100%;
}
